.containerMedium {
    max-width: $ds + 146px;
    min-width: $sm;
    margin: 0 auto;
    padding: 0 20px;

    @media (max-width: $tb) {
        padding: 0 10px;
    }
}

.containerBig {
  max-width: $ds + 436px;
  min-width: $sm;
  margin: 0 auto;
  padding: 0 20px;

  @media (max-width: $tb) {
      padding: 0 10px;
  }
}

img {
    max-width: 100%;
    height: auto;
  }

  .visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

  .visually-hidden:not(:focus):not(:active),
  input[type="checkbox"] .visually-hidden,
  input[type="radio"].visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

.page {
        font-size: 10px;
        line-height: 1.15;
        -webkit-text-size-adjust: 100%;
        &__body {
                margin: 0;
                font-family: "Roboto", "RobotoCondensed", "PT Mono", "Arial", sans-serif;
        }
}

.link {
  text-decoration: none;
  cursor: pointer;
  &:hover {
    opacity: .4;
  }
}
